import { Injectable } from '@angular/core';
import { HttpClient, HttpInterceptor, HttpRequest, HttpHandler, HttpEvent, HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { UserService } from 'src/app/shared/services/user.service';
import { Observable } from 'rxjs/internal/Observable';
import { map, catchError } from 'rxjs/operators';
import { of } from 'rxjs';
import { Router } from '@angular/router';
import { Storage } from '@ionic/storage';
@Injectable({
  providedIn: 'root',
})
export class HttpService implements HttpInterceptor {
  baseURL = 'http://3.7.228.209:3000/';
  constructor(private http: HttpClient, private user: UserService, private router: Router, private storage: Storage) {}
  apiURL = 'http://3.7.228.209:3000/';
  dummyURL = 'assets/data/';
  token = '';
  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    if (this.token === '') {
      this.storage.get('token')
      .then((val)=>{
        if (val) {
          console.log('Token from local storage',val);
          this.token = val;
        }
      })
      
    }
    let apiReq = req.clone({
      url: req.url.endsWith('.json')
        ? this.dummyURL + req.url
        : this.apiURL + req.url,
    });
    if (!req.url.includes('signin') && this.token) {
      apiReq = apiReq.clone({
        setHeaders: {
          Authorization: `Bearer ${this.token}`,
        },
      });
    }
    return next.handle(apiReq).pipe(
      map((event: HttpEvent<any>) => {
        if (event instanceof HttpResponse) {
          if (event.body) {
            if (event.body.rstatus === 0) {
              if (event.body.message.includes('Your token has been expired')) {
                this.token = '';
                this.storage.remove('token');
                setTimeout(() => {
                  this.router.navigateByUrl('signin');
                }, 3000);
              }
              throw of(new Error(event.body.message));
            }
            const modifiedResponse = event.body.data;
            const modEvent = event.clone({ body: modifiedResponse });
            return modEvent;
          }
          return event;
        }
      }),
      catchError((err: any) => {
        if (err instanceof HttpErrorResponse) {
          try {
            console.log(err);
            if (err.status === 401) {
              this.token = '';
              this.storage.remove('token');
              this.router.navigateByUrl('signin');
              // alert(err.statusText);
            } else if (err.status === 500) {
              
            }
          } catch (e) {
            console.log(e);
          }
        }
        return of(err);
      })
    );
  }

  get(path: string): Observable<any> {
    return this.http.get(`${this.dummyURL}${path}`);
  }

}
