import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from 'src/app/shared/services/user.service';
import { HttpClient } from '@angular/common/http';
import { Storage } from '@ionic/storage';
import { FacebookLoginProvider, GoogleLoginProvider } from "angularx-social-login";
import { SocialAuthService } from "angularx-social-login";

@Component({
  templateUrl: './signin.component.html',
})
export class SigninComponent implements OnInit {
  userInputs = [
    { type: 'text', name: 'username', label: 'Email or Phone Number' },
    { type: 'password', name: 'password', label: 'Password' },
  ];
  signinForm: FormGroup;
  isFormValid = true;
  errorText: string;
  constructor(private formBuilder: FormBuilder, private router: Router, private user: UserService, private http: HttpClient, private storage: Storage, private authService: SocialAuthService) {}



  ngOnInit(): void {
    this.signinForm = this.formBuilder.group({
      username: new FormControl('', [Validators.required, Validators.pattern(/^(\d{10}|\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3}))$/)]),
      password: new FormControl('', [Validators.required]),
    });
  }


  async submitForm(): Promise<void> {
    // console.log('hello')
    this.isFormValid = this.signinForm.valid;
    if (this.isFormValid) {
      // console.log('hey im valid');

      // this.isFormValid = await this.signin(this.signinForm.value);
      this.http.post('auth/sign_in', {login: this.signinForm.value.username, password: this.signinForm.value.password})
      .subscribe((results: any)=>{
        this.storage.set('token', results.token);
        this.router.navigateByUrl('/');
      })
      // if (this.isFormValid) {
      //   this.router.navigateByUrl('/');
      // } else {
      //   this.errorText = 'Invalid credentials';
      // }
    } else {
      this.errorText = 'Please enter valid credentials';
    }
  }
  // for demo purpose
  // async signin(form: any): Promise<boolean> {
  //   const user = await this.user.getUser();
  //   if (user) {
  //     if (form.username === user.email || form.username === user.phoneNumber.toString()) {
  //       if (form.password === user.password) {
  //         console.log('Form value ', form.username, form.password);
  //         return true;
  //       }
  //     }
  //   }
  //   return false;
  // }
  // to be implemented
  socialSignin(platform: string): void {
    this.authService.signIn(GoogleLoginProvider.PROVIDER_ID);
    this.authService.authState.subscribe((user) => {
      console.log('User ',user)
    });
  }
}
