import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { PasswordValidator } from 'src/app/shared/classes/password-validator';
import { Router } from '@angular/router';
import { UserService } from 'src/app/shared/services/user.service';
import { HttpClient } from '@angular/common/http';
import { SocialAuthService } from 'angularx-social-login';
import { FacebookLoginProvider, GoogleLoginProvider } from "angularx-social-login";

@Component({
  templateUrl: './signup.component.html',
})
export class SignupComponent implements OnInit {
  userInputs = [
    { type: 'text', name: 'first_name', label: 'First Name' },
    { type: 'text', name: 'last_name', label: 'Last Name' },
    { type: 'number', name: 'mobile', label: 'Phone number' },
    { type: 'password', name: 'password', label: 'Password' },
    { type: 'password', name: 'confirmPassword', label: 'Confirm password' },
    { type: 'email', name: 'email', label: 'Email' },
  ];
  signupForm: FormGroup;
  isFormValid = true;
  errorText: string;
  constructor(private formBuilder: FormBuilder, private router: Router, private user: UserService, private http: HttpClient,private authService: SocialAuthService) {}
  ngOnInit(): void {
    this.signupForm = this.formBuilder.group(
      {
        first_name: new FormControl('', [Validators.required]),
        last_name: new FormControl('', [Validators.required]),
        mobile: new FormControl('', [Validators.required, Validators.pattern(/^(\d{10})$/)]),
        password: new FormControl('', [Validators.required]),
        confirmPassword: new FormControl('', [Validators.required]),
        email: new FormControl('', [Validators.email, Validators.required]),
      },
      {
        validators: PasswordValidator.doesMatch,
      }
    );
  }
  async submitForm() {
    this.isFormValid = this.signupForm.valid;
    if (this.isFormValid) {
      console.log({
        first_name: this.signupForm.value.first_name,
        last_name: this.signupForm.value.last_name,
        mobile: this.signupForm.value.mobile,
        email: this.signupForm.value.email,
        user_type: 'buyer',
        password: this.signupForm.value.password,
        password_confirmation: this.signupForm.value.confirmPassword,
        is_contractor: 'true'
      });
      this.http.post('auth', {
        first_name: this.signupForm.value.first_name,
        last_name: this.signupForm.value.last_name,
        mobile: this.signupForm.value.mobile,
        email: this.signupForm.value.email,
        user_type: 'buyer',
        password: this.signupForm.value.password,
        password_confirmation: this.signupForm.value.confirmPassword,
        is_contractor: 'true'
      })
      .subscribe((results: any)=>{
        console.log(results);
        this.router.navigateByUrl('/signin');
      })
      // this.isFormValid = await this.signup(this.signupForm.value);
      // if (this.isFormValid) {
      //   this.router.navigateByUrl('/verify-otp');
      // } else {
      //   this.errorText = 'User could not be added';
      // }
    } else {
      // if (Object.values(this.signupForm.controls).filter((x) => x.errors).length === 1 && this.signupForm.controls.confirmPassword.errors) {
      //   this.errorText = 'Passwords do not match!';
      // } else {
      //   this.errorText = 'Please fill in all required details';
      // }
      return false;
    }
  }
  // for demo purpose
  async signup(form: any): Promise<boolean> {
    delete form.confirmPassword;
    return await this.user.setUser(form);
  }
  // to be implemented
  socialSignup(platform: string): void {
    this.authService.signIn(GoogleLoginProvider.PROVIDER_ID);
    this.authService.authState.subscribe((user) => {
      console.log('User ',user)
    });
  }
}
