import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { PasswordValidator } from 'src/app/shared/classes/password-validator';
import { UserService } from 'src/app/shared/services/user.service';
import { Router } from '@angular/router';

@Component({
  templateUrl: './reset.component.html',
})
export class ResetComponent implements OnInit {
  userInputs = [
    { type: 'password', name: 'password', label: 'Password' },
    { type: 'password', name: 'confirmPassword', label: 'Confirm Password' },
  ];
  resetPasswordForm: FormGroup;
  isFormValid = true;
  errorText: string;
  userEmail: string;
  constructor(private formBuilder: FormBuilder, private user: UserService, private router: Router) {}
  ngOnInit(): void {
    this.resetPasswordForm = this.formBuilder.group(
      {
        password: new FormControl('', [Validators.required]),
        confirmPassword: new FormControl('', [Validators.required]),
      },
      {
        validators: PasswordValidator.doesMatch,
      }
    );
    this.getEmail();
  }
  async getEmail(): Promise<void> {
    this.userEmail = (await this.user.getUser()).email;
  }
  async submitForm(): Promise<void> {
    this.isFormValid = this.resetPasswordForm.valid;
    if (this.isFormValid) {
      this.isFormValid = await this.checkNewPassword(this.resetPasswordForm.value);
      if (this.isFormValid) {
        this.router.navigateByUrl('/signin');
      } else {
        this.errorText = 'Passwords could not be reset';
      }
    } else {
      if (
        Object.values(this.resetPasswordForm.controls).filter((x) => x.errors).length === 1 &&
        this.resetPasswordForm.controls.confirmPassword.errors
      ) {
        this.errorText = 'Passwords do not match!';
      } else {
        this.errorText = 'Please enter new password';
      }
    }
  }
  // for demo purpose
  async checkNewPassword(form: any): Promise<boolean> {
    return form.password.length >= 8;
  }
}
