import { Pipe, PipeTransform } from '@angular/core';
import { Location } from 'src/app/shared/models/location';

@Pipe({
  name: 'validLocation',
})
export class ValidLocationPipe implements PipeTransform {
  transform(arr: Location[], input: string): Location[] | any {
    input = input.toLowerCase();
    if (arr) {
      const filteredArr = arr.filter((el) => {
        return el.area.toLowerCase().includes(input) || el.pincode.toString().includes(input);
      });
      if (filteredArr.length) {
        return filteredArr;
      } else {
        return [{ errorMessage: 'Please choose another location' }];
      }
    } else {
      return null;
    }
  }
}
