import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from 'src/app/shared/services/user.service';

@Component({
  templateUrl: './forgot.component.html',
})
export class ForgotComponent implements OnInit {
  userInputs = [{ type: 'email', name: 'email', label: 'Email' }];
  forgotPasswordForm: FormGroup;
  isFormValid = true;
  errorText: string;
  constructor(private formBuilder: FormBuilder, private router: Router, private user: UserService) {}
  ngOnInit(): void {
    this.forgotPasswordForm = this.formBuilder.group({
      email: new FormControl('', [Validators.required, Validators.email]),
    });
  }
  async submitForm(): Promise<void> {
    this.isFormValid = this.forgotPasswordForm.valid;
    if (this.isFormValid) {
      this.isFormValid = await this.validateEmail(this.forgotPasswordForm.value);
      if (this.isFormValid) {
        this.router.navigateByUrl('/reset-password');
      } else {
        this.errorText = 'Email not registered';
      }
    } else {
      this.errorText = 'Please enter valid email';
    }
  }
  // for demo purpose
  async validateEmail(form: any): Promise<boolean> {
    const user = await this.user.getUser();
    return user && form.email === user.email;
  }
}
