import { Component, OnInit } from '@angular/core';
import { Location } from 'src/app/shared/models/location';
import { Observable } from 'rxjs/internal/Observable';
import { HttpService } from 'src/app/shared/services/http.service';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { UserService } from 'src/app/shared/services/user.service';
import { Router } from '@angular/router';

@Component({
  templateUrl: './location.component.html',
  styleUrls: ['./location.component.scss'],
})
export class LocationComponent implements OnInit {
  locations: Observable<Location[]>;
  userInputs = [{ type: 'text', name: 'location', label: 'Location/Pincode' }];
  locationForm: FormGroup;
  isFormValid = true;
  errorText: string;
  userName: string;
  areSuggestionsVisible = false;
  constructor(private http: HttpService, private formBuilder: FormBuilder, private user: UserService, private router: Router) {}
  ngOnInit(): void {
    this.locations = this.http.get('locations.json');
    this.locationForm = this.formBuilder.group({
      location: new FormControl('', [Validators.required]),
    });
    this.getUserName();
  }
  // for demo purpose
  async getUserName(): Promise<void> {
    this.userName = (await this.user.getUser()).name;
  }
  pickLocation(selection: Location): void {
    this.errorText = '';
    this.areSuggestionsVisible = false;
    this.locationForm.setValue({
      location: selection.area,
    });
  }
  detectClicks(event: any): void {
      this.areSuggestionsVisible = event.target.nodeName === 'INPUT';
  }
  async submitForm(): Promise<void> {
    this.isFormValid = this.locationForm.valid;
    if (this.isFormValid) {
      this.isFormValid = await this.checkLocation(this.locationForm.value);
      if (this.isFormValid) {
        this.router.navigateByUrl('/');
        return null;
      }
    }
    this.errorText = 'Please select a location from the dropdown';
  }
  // for demo purpose
  async checkLocation(form: any): Promise<boolean> {
    const locations = await this.locations.toPromise();
    const isValid = locations.filter((location) => location.area === form.location).length === 1;
    this.locationForm.controls.location.setErrors(!isValid ? { isInvalid: true } : null);
    return isValid;
  }
}
