import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { IonicModule, IonicRouteStrategy } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';

import { ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { AppRoutingModule } from './app.routing';

import { AppComponent } from './app.component';
import { SigninComponent } from './pages/signin/signin.component';
import { ForgotComponent } from './pages/forgot/forgot.component';
import { ResetComponent } from './pages/reset/reset.component';
import { SignupComponent } from './pages/signup/signup.component';
import { OtpComponent } from './pages/otp/otp.component';
import { LocationComponent } from './pages/location/location.component';

import { SigninWrapperComponent } from './shared/components/signin-wrapper/signin-wrapper.component';
import { SubmitButtonComponent } from './shared/components/submit-button/submit-button.component';
import { SocialButtonsComponent } from './shared/components/social-buttons/social-buttons.component';

import { SubmitFormDirective } from './shared/directives/submit-form.directive';
import { ValidLocationPipe } from './shared/pipes/valid-location.pipe';
import { HttpService } from './shared/services/http.service';
import { IonicStorageModule } from '@ionic/storage';
import { SocialLoginModule, SocialAuthServiceConfig } from 'angularx-social-login';
import {
  GoogleLoginProvider,
  FacebookLoginProvider,
  AmazonLoginProvider,
} from 'angularx-social-login';
@NgModule({
  declarations: [
    AppComponent,
    SigninComponent,
    ForgotComponent,
    ResetComponent,
    SignupComponent,
    OtpComponent,
    LocationComponent,
    SigninWrapperComponent,
    SubmitButtonComponent,
    SocialButtonsComponent,
    SubmitFormDirective,
    ValidLocationPipe,
  ],
  entryComponents: [],
  imports: [
    BrowserModule,
    IonicModule.forRoot(),
    IonicStorageModule.forRoot(),
    ReactiveFormsModule,
    HttpClientModule,
    AppRoutingModule,
    HttpClientModule,
    SocialLoginModule,
    FormsModule
  ],
  providers: [
    {
      provide: 'SocialAuthServiceConfig',
      useValue: {
        autoLogin: false,
        providers: [
          {
            id: GoogleLoginProvider.PROVIDER_ID,
            provider: new GoogleLoginProvider(
              '605933383346-v9b4vlgdsd8nscjt7edc5uchompfd6ru.apps.googleusercontent.com'
            ),
          }
        ],
      } as SocialAuthServiceConfig,
    },
    StatusBar,
    SplashScreen,
    {
      provide: RouteReuseStrategy,
      useClass: IonicRouteStrategy
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: HttpService,
      multi: true,
    },
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}
