import { Injectable, EventEmitter } from '@angular/core';
import { User } from './../models/user';

import { Plugins } from '@capacitor/core';
const { Storage } = Plugins;

@Injectable({
  providedIn: 'root',
})
export class UserService {
  private user: User;
  private token: string;

  closePopover = new EventEmitter();

  // this function must be called on app load
  fetchLocalStorage() {
    this.getObject('user').then((user) => {
      this.user = user ? user : null;
    });
    this.getObject('token').then((token) => {
      this.token = token ? token.token : null;
    });
  }

  getUser(): User {
    if (this.user) {
      return this.user;
    }
  }
  getToken(): string {
    if (this.token) {
      return this.token;
    }
  }

  logout() {
    delete this.user;
    return true;
  }

  // async call to Storage APIs
  async setUser(user: User): Promise<any> {
    this.user = user;
    await this.setObject('user', user).then((response: boolean) => {
      return response;
    });
    // temp fix [TODO]
    return true;
  }
  async setToken(token: string): Promise<any> {
    this.token = token;
    await this.setObject('user', token).then((response: boolean) => {
      return response;
    });
  }
  private async getObject(key: string): Promise<any> {
    const userStore = await Storage.get({ key });
    return JSON.parse(userStore.value);
  }
  private async setObject(key: string, value: any): Promise<boolean> {
    return await Storage.set({
      key,
      value: JSON.stringify(value),
    })
      .then((res) => true)
      .catch((res) => false);
  }
  async clear(): Promise<void> {
    await Storage.clear();
  }
}
