import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

import { AuthGuard } from './shared/guards/auth.guard';

import { SigninComponent } from './pages/signin/signin.component';
import { ForgotComponent } from './pages/forgot/forgot.component';
import { ResetComponent } from './pages/reset/reset.component';
import { SignupComponent } from './pages/signup/signup.component';
import { OtpComponent } from './pages/otp/otp.component';
import { LocationComponent } from './pages/location/location.component';

const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('./contractor/contractor.module').then((m) => m.ContractorModule),
  },
  { path: 'signin', component: SigninComponent },
  { path: 'forgot-password', component: ForgotComponent },
  { path: 'reset-password', component: ResetComponent },
  { path: 'create-new-account', component: SignupComponent },
  { path: 'verify-otp', component: OtpComponent },
  { path: 'set-location', component: LocationComponent },
  { path: '**', redirectTo: '', pathMatch: 'full' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })],
  exports: [RouterModule],
})
export class AppRoutingModule {}
