import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { UserService } from 'src/app/shared/services/user.service';
import { Router } from '@angular/router';

@Component({
  templateUrl: './otp.component.html',
})
export class OtpComponent implements OnInit {
  userInputs = [{ type: 'number', name: 'otp', label: 'OTP' }];
  otpVerificationForm: FormGroup;
  isFormValid = true;
  errorText: string;
  userNumber: number;
  constructor(private formBuilder: FormBuilder, private user: UserService, private router: Router) {}
  ngOnInit(): void {
    this.otpVerificationForm = this.formBuilder.group({
      otp: new FormControl('', [Validators.required, Validators.pattern(/^(\d{6})$/)]),
    });
    this.getNumber();
  }
  // for demo purpose
  async getNumber(): Promise<void> {
    this.userNumber = (await this.user.getUser()).phoneNumber;
  }
  async submitForm(): Promise<void> {
    this.isFormValid = this.otpVerificationForm.valid;
    if (this.isFormValid) {
      this.isFormValid = await this.checkOTP(this.otpVerificationForm.value);
      if (this.isFormValid) {
        this.router.navigateByUrl('/set-location');
      } else {
        this.errorText = 'OTP is not valid';
      }
    } else {
      this.errorText = 'Please enter the OTP received';
    }
  }
  // for demo purpose
  async checkOTP(form: any): Promise<boolean> {
    return this.userNumber.toString().endsWith(form.otp);
  }
}
